import {apiUsers} from "../components/api/api";
import {bedug_mode} from "./store-redux";

const SET_FRIENDS = "myApp/users-reducer/SET_FRIENDS"; //  
const FRIENDS_INITIAL_STATE = "myApp/users-reducer/FRIENDS_INITIAL_STATE"; //   

export let setFriends = (users) => {//    
  return {type: SET_FRIENDS, users}
};
export let friendsInitialState = (users) => {//    
  return {type: FRIENDS_INITIAL_STATE, users}
};

let initialState = {
  myFriends2: [], //   
  friendsCurrentPage: 1, //    
  friendsPageSize: 50, //       
  friendsTerm: "", //   
  friend: true //    ( ,  follow = true)

}
const sidebarReducer = (state = initialState, action) => {
  let stateCopy; //      
  switch (action.type) {
    case SET_FRIENDS:
      stateCopy = {...state, myFriends2: action.users};
      if (bedug_mode) {console.log("sidebar-reducer.js, SET_FRIENDS: ", state, stateCopy)} // 
      return stateCopy; //   
    case FRIENDS_INITIAL_STATE:
      stateCopy = initialState;
      if (bedug_mode) {console.log("sidebar-reducer.js, FRIENDS_INITIAL_STATE: ", state, stateCopy)} // 
      return stateCopy; //   
    default:
      return state;
  }
}

export let getFriendsThunkCreator = (currentPage, pageSize, term, friend) => {//    
  let getUsersThunk = (dispatch) => { //   
    if (bedug_mode) {console.log("getFriendsThunkCreator")}

    apiUsers.getUsers(currentPage, pageSize, term, friend) //       
      .then((data) => {
        if (bedug_mode) {console.log("sidebar-reducer.js, getFriendsThunkCreator getUsers().then: dispatch(setFriends( -> SET_FRIENDS" )} // 
        dispatch(setFriends(data.items))//     
      })
/*
      .catch((error)=>{
        console.log("=======================>", error) //    ,  then  
      })
*/
  }
  return getUsersThunk
}

export default sidebarReducer;
