import {bedug_mode} from "../../redux/store-redux";
import GetDate from "./GetDate";

export let apiDialogs = { //    api  DialogList
  _setMessages2LS: (Dialog_2, myId, userId) => { //      LocalStorage    
    let LocalStoragedialogUpdateTime1 =
      GetDate().Day + "." +
      GetDate().Month + "" +
      GetDate().Year + " " +
      GetDate().Hour + ":" +
      GetDate().Minutes + ":" +
      GetDate().Seconds

    //        

    let dialogNameLocal = myId > userId ? "Dialog_" + myId + "_" + userId : "Dialog_" + userId + "_" + myId;  //     
    let dialogUpdateTimeLocal = myId > userId ? "Dialog_" + myId + "_" + userId + "_UpdateTime" : "Dialog_" + userId + "_" + myId + "_UpdateTime";//       

    localStorage.setItem(dialogNameLocal, JSON.stringify(Dialog_2)); //     LocalStorage
    localStorage.setItem(dialogUpdateTimeLocal, JSON.stringify(LocalStoragedialogUpdateTime1)); //      LocalStorage

    let Dialog_3 = apiDialogs.getDialog(myId, userId); //     LocalStorage

    return Dialog_3 //   
  },
  getDialog: (myId, userId) => {
    let dialogNameLocal = myId > userId ? "Dialog_" + myId + "_" + userId : "Dialog_" + userId + "_" + myId; //     
    let LocalStorageDialogs1 = JSON.parse(localStorage.getItem(dialogNameLocal)); //    LocalStorage   
    if (!LocalStorageDialogs1) { //       (undefined)
      LocalStorageDialogs1 = []; //    
    }
    return LocalStorageDialogs1 //     Dialog_25528_1079
  },
  getUpdateTime: (myId, userId) => {
    let dialogUpdateTimeLocal = myId > userId ? "Dialog_" + myId + "_" + userId + "_UpdateTime" : "Dialog_" + userId + "_" + myId + "_UpdateTime"; //       
    let LocalStoragedialogUpdateTime1 = JSON.parse(localStorage.getItem(dialogUpdateTimeLocal)); //       
    if (!LocalStoragedialogUpdateTime1) { //        (undefined)
      LocalStoragedialogUpdateTime1 = null; //    
    }
    return LocalStoragedialogUpdateTime1 //     
  },

  deleteMessage: (messageID, myId, userId) => {
    let Dialog_1 = apiDialogs.getDialog(myId, userId); //   Dialog_25528_1079  LocalStorage
    let Dialog_2 = Dialog_1.filter(message => message.id !== messageID) //  ,     

    let Dialog_3 = apiDialogs._setMessages2LS(Dialog_2, myId, userId)//      (LocalStorage)


    if (Dialog_3.length===0) { //         

      //       LocalStorage
      let dialogListUserId1 = "DialogList_" + userId //   DialogList
      let Data1 = JSON.parse(localStorage.getItem(dialogListUserId1)); //       
      if (!Data1) { //     
        Data1 = [] //      ,  
      }
      let Data2 = Data1.filter(d=>d.userId!==myId) //      ,  ,  0 , 
      localStorage.setItem(dialogListUserId1, JSON.stringify(Data2)); //   LocalStorage  

      //       LocalStorage
      let dialogUpdateTimeLocal = myId > userId ? "Dialog_" + myId + "_" + userId + "_UpdateTime" : "Dialog_" + userId + "_" + myId + "_UpdateTime"; //       
      localStorage.removeItem(dialogUpdateTimeLocal); //     ,      

      //    Dialog_25528_1079  LocalStorage
      let dialogNameLocal = myId > userId ? "Dialog_" + myId + "_" + userId : "Dialog_" + userId + "_" + myId;  //     
      localStorage.removeItem(dialogNameLocal); //    Dialog_25528_1079  LocalStorage,      
    }

    return Dialog_3 //     
  },

  updateDialogListUserId: (userId1, userId2, Name2, Photo2) => { //      ,      
    let dialogListUserId1 = "DialogList_" + userId1 //   DialogList
    let Data1 = JSON.parse(localStorage.getItem(dialogListUserId1)); //       
    if (!Data1) {
      Data1 = [] //      ,  
    }
    let newDialogLocal = { //    
      dialogId: Data1.length + 1, // id   dialogList
      userId: userId2, // Id     DialogList
      userName: Name2, //      DialogList
      userPhoto: Photo2 //      DialogList
    }

    let shouldDataUpdate = true; //

    Data1.map((d1) => { //      ,       ,    
      if (d1.userId === userId2) {
        shouldDataUpdate = false;
        return null
      }
      return null
    })

    if (shouldDataUpdate) { //     true,     
      let Data2 = [...Data1, newDialogLocal];
      localStorage.setItem(dialogListUserId1, JSON.stringify(Data2)); //    LocalStorage
    }
  },

  getDialogListMyID: (myId) => { //     ,   
    let dialogListUserId1 = "DialogList_" + myId //    DialogList
    let Data1 = JSON.parse(localStorage.getItem(dialogListUserId1)); //     
    if (!Data1) {
      Data1 = [] //      ,  
    }
    return Data1 //   
  },

  postDialog: (formDataNewMessage, myId, MyName, MyPhoto, userId) => { //   LocalStorage

    let Dialog_1 = apiDialogs.getDialog(myId, userId); //   Dialog_25528_1079  LocalStorage

    if (bedug_mode) {
      console.log("apiLocalStorage Dialog_1(getItem): ", Dialog_1)
    } // 

    let Dialog_2 = [...Dialog_1, {//      
      id: Dialog_1.length + 1, //  id 
      Date: GetDate(), //   
      userId: myId, //    
      message: formDataNewMessage //    
    }]
    let Dialog_3 = apiDialogs._setMessages2LS(Dialog_2, myId, userId)//      (LocalStorage)

    apiDialogs.updateDialogListUserId(userId, myId, MyName, MyPhoto)
    //  DialogList ,  .    ,    

    //    DialogList,         .

    return Dialog_3 //     DAL  BLL

  },

  deleteDialog: (dialogId, userId1, userId2) => { //       ID
    let Data1 = apiDialogs.getDialogListMyID(userId1)
  // console.log(Data1)
    let Data2 = Data1.filter(d=>d.dialogId!==dialogId)
    //console.log(Data2)
    localStorage.setItem("DialogList_" + userId1, JSON.stringify(Data2)); //    LocalStorage

  },

}

export let apiCommon = { //    api   
  putInfoMode: (info_mode) => { //  info_mode  localStorage
    localStorage.setItem("putInfoMode", JSON.stringify(info_mode)); //  info_mode  LocalStorage
    let Data1 = JSON.parse(localStorage.getItem("putInfoMode")); //  info_mode  LocalStorage
    return Data1
  },
  getInfoMode: () => { //  info_mode  localStorage
    let Data1 = JSON.parse(localStorage.getItem("putInfoMode")); //  info_mode  LocalStorage
    if (Data1===undefined|| Data1===0 || Data1===null) {
      Data1=true
    }
    apiCommon.putInfoMode(Data1)
    return Data1
  }
}
