import {apiProfile} from "../components/api/api";
import {bedug_mode} from "./store-redux";
import {getAuthMeThunkCreator, setMyProfile} from "./auth-reducer";
import {updateDialogListThunkCreator} from "./dialogs-reducer";

const DELETE_POST = "myApp/profile-reducer/DELETE_POST";//    
const ADD_POST = "myApp/profile-reducer/ADD-POST";//    
const SET_USER_PROFILE = "myApp/profile-reducer/SET_USER_PROFILE"; //        
const SET_STATUS = "myApp/profile-reducer/SET_STATUS" //    
const PROFILE_INITIAL_STATE = "myApp/profile-reducer/PROFILE_INITIAL_STATE" //    
const SET_PROFILE_PHOTO = "myApp/profile-reducer/SET_PROFILE_PHOTO" //    
const SET_EDIT_PROFILE_ERROR= "myApp/auth-reducer/SET_EDIT_PROFILE_ERROR"; //    

export let setEditProfileStatus = (editProfileStatus) => { //           
    return {type: SET_EDIT_PROFILE_ERROR, editProfileStatus}
};
export let deletePostActionCreator = (postId) => { //     postId
    return {type: DELETE_POST, postId}
};
export let addPostActionCreator = (newPostData) => { //   
    return {type: ADD_POST, newPostData}
};
let setUserProfile = (profile) => { //        
    return {type: SET_USER_PROFILE, profile}
};
export let setStatus = (newStatus) => { //    ( API )
    return {type: SET_STATUS, newStatus}
};
export let profileInitialState = () => { //   
    return {type: PROFILE_INITIAL_STATE}
};
export let setProfilePhoto = () => { //   
    return {type: SET_PROFILE_PHOTO}
};

let initialState = {
    posts: [//     
        {id: 1, message: "state 2 Hi, how are you?", like: "12"},
        {id: 2, message: "state 2 it's, my first post", like: "15"},
    ],
    profile: null, //      
    status: null, //      
    editProfileStatus: [], //       

}
export let profileReducer = (state = initialState, action) => { //  
    let stateCopy; //      
    switch (action.type) {
        case SET_USER_PROFILE: //       
            stateCopy = {
                ...state,
                profile: action.profile //     
            }
            if (bedug_mode) {console.log("profile-reducer.jsx, SET_USER_PROFILE: ", state, stateCopy)} // 
            return stateCopy;
        case ADD_POST: {//  
            let newPost = { //     
                id: 5, // id    ()
                message: action.newPostData, //     
                like: 2 //   ()
            };
            stateCopy = {
                ...state,
                posts: [...state.posts, newPost], //      
            }
            if (bedug_mode) {console.log("profile-reducer.jsx, ADD_POST: ", state, stateCopy)} // 
            return stateCopy;
        }
        case DELETE_POST: {//    postId
            stateCopy = {
                ...state,
                posts: [...state.posts.filter(f=>f.id!==action.postId)], //     
            }
            if (bedug_mode) {console.log("profile-reducer.jsx, DELETE_POST: ", state, stateCopy)} // 
            return stateCopy;
        }
        case SET_STATUS: //      ( API )
            stateCopy = {
                ...state,
                status: action.newStatus
            }
            if (bedug_mode) {console.log("profile-reducer.jsx, SET_STATUS: ", state, stateCopy)} // 
            return stateCopy;
        case PROFILE_INITIAL_STATE: //  
            stateCopy = initialState
            if (bedug_mode) {console.log("profile-reducer.jsx, PROFILE_INITIAL_STATE: ", state, stateCopy)} // 
            return stateCopy;
        case SET_EDIT_PROFILE_ERROR: //          
            stateCopy = {
                ...state,
                editProfileStatus: action.editProfileStatus
            }
            if (bedug_mode) {
                console.log("auth-reducer.jsx, SET_EDIT_PROFILE_ERROR: ", state, stateCopy)
            } // 
            return stateCopy; //     
        default:
            return state;
    }
}

export let getProfileThunkCreator = (userId, shouldUpdateDialogList, myId) => { //      
    return async (dispatch) => { //       

        let CommonPart = (response, userId) => { //         
            if (bedug_mode) {console.log("profile-reducer.jsx, getProfileThunkCreator.await getAuthMe()->await .getProfile() :   dispatch(setUserProfile()) ->SET_USER_PROFILE" )} // 
            dispatch(setUserProfile(response)) //     
            if (bedug_mode) {console.log("profile-reducer.jsx, getProfileThunkCreator.await getAuthMe()->await .getProfile() :   dispatch(getStatusThunkCreator())" )} // 
            dispatch(getStatusThunkCreator(userId)) //   
            if (shouldUpdateDialogList) {//     
                dispatch(updateDialogListThunkCreator(myId, response.userId, response.fullName, response.photos.small  )) //  

            }
        }

        if (!userId) { //  userId    URL (       ID  )
            const response = await apiProfile.getAuthMe() //    " ?"    ID ( )
            if (response.resultCode === 0) { //        ID
                userId = response.data.id; //   ID  userId
                const response2 = await apiProfile.getProfile(userId) //      
                CommonPart(response2, userId)  //         
            }
        } else { //  userId   URL
            const response = await apiProfile.getProfile(userId) //
            CommonPart(response, userId) //         
        }
    }
}

export let getStatusThunkCreator = (userId) => {  //     
    return async (dispatch) => { //      
        const response = await apiProfile.getStatus(userId) // api     userId
        if (bedug_mode) {console.log("profile-reducer.jsx, getStatusThunkCreator.await getStatus(): dispatch(setStatus()) -> SET_STATUS" )} // 
        dispatch(setStatus(response)) //        
    }
}
export let putStatusThunkCreator = (statusTmpInput, myId) => { //    
    return async (dispatch) => { //     
        const response = await apiProfile.putStatus(statusTmpInput) //     
        if (response.resultCode === 0) { //      
            if (bedug_mode) {console.log("profile-reducer.jsx, putStatusThunkCreator.await putStatus(): dispatch(getStatusThunkCreator())" )} // 
            dispatch(getStatusThunkCreator(myId))//       
        }
    }
}
export let setprofilePhotoThunkCreator = (profilePhoto, myId) => { //     
    return async (dispatch) => { //      
        const response = await apiProfile.putPhoto(profilePhoto) //     
        if (response.resultCode === 0) { //      
            if (bedug_mode) {console.log("profile-reducer.jsx, setprofilePhotoThunkCreator.await putPhoto(): dispatch(getProfileThunkCreator())" )} // 
            dispatch(getProfileThunkCreator(myId));//      
            dispatch(getAuthMeThunkCreator()) //     (header photo)   
        }
    }
}

export let putMyProfileThunkCreator = (MyProfile, myId) => { //     myProfile
    return async (dispatch) => { //      myProfile
        const response = await apiProfile.putMyProfileData(MyProfile) //     
        if (response.resultCode === 0) { //      
            if (bedug_mode) {
                console.log("auth-reducer.jsx, putMyProfileThunkCreator.await putMyProfileData(): dispatch(getProfileThunkCreator())")
            } // 
            const response2 = await apiProfile.getProfile(myId)//       
            if (bedug_mode) {
                console.log("auth-reducer.jsx, putMyProfileThunkCreator.await(putMyProfileData)->await .getProfile() : dispatch(setMyProfile()->SET_MY_PROFILE")
            } // 
            dispatch(setMyProfile(response2))//     
            dispatch(getProfileThunkCreator(myId))
            dispatch(setEditProfileStatus(["Edited successfully!"])) //     
        } else { //         
            let message =  //    message -   
                !response.messages===0 //     
                    ? "no responce from server" //   
                    : response.messages //     
            if (bedug_mode) {
                console.log("auth-reducer.jsx, putMyProfileThunkCreator.await /    :", message) //    
            } // 
            dispatch(setEditProfileStatus(message)) //     
        }
    }
}


export default profileReducer;










