import {apiUsers} from "../components/api/api";
import {bedug_mode} from "./store-redux";

const SET_TERM = "myApp/users-reducer/SET_TERM";
const SET_USERS = "myApp/users-reducer/SET_USERS";
const SET_CURRENT_PAGE = "myApp/users-reducer/SET_CURRENT_PAGE";
const SET_TOTAL_USERS_COUNT = "myApp/users-reducer/SET_TOTAL_USERS_COUNT";
const TOGGLE_IS_FETCHING = "myApp/users-reducer/TOGGLE_IS_FETCHING";
const TOGGLE_IS_FOLLOWING_PROGRESS = "myApp/users-reducer/TOGGLE_IS_FOLLOWING_PROGRESS";
const NEED_UPDATE_FRIENDS = "myApp/users-reducer/NEED_UPDATE_FRIENDS";
const USERS_INITIAL_STATE = "myApp/users-reducer/USERS_INITIAL_STATE";
const SET_ONLY_FRIENDS = "myApp/users-reducer/SET_ONLY_FRIENDS";//     ,   


export let setTerm = (term) => {
    return {type: SET_TERM, term}
};
let setUsers = (users) => {
    return {type: SET_USERS, users}
};
export let setCurrentPage = (currentPage) => {
    return {type: SET_CURRENT_PAGE, currentPage}
};
let setUsersTotalCount = (totalUsersCount) => {
    return {type: SET_TOTAL_USERS_COUNT, totalUsersCount}
};
let toggleIsFetching = (isFetching) => {
    return {type: TOGGLE_IS_FETCHING, isFetching}
};
let toggleIsFollowingProgerss = (isFetching, id) => {
    return {type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, id}
};
export let needUpdateFriendsAC = (needUpdateFriends) => {
    return {type: NEED_UPDATE_FRIENDS, needUpdateFriends}
};
export let usersInitialState = () => {
    return {type: USERS_INITIAL_STATE}
};
export let setOnlyFriends = (onlyFriends) => { //      ,   
    return {type: SET_ONLY_FRIENDS, onlyFriends}
};

let initialState = {
    users: [], //     ( )
    pageSize: 12, //       
    totalUsersCount: 0, //     
    currentPage: 1, //      
    isFetching: false, //   ()
    followingInProgress: [], //   ,    follow/unfollow  disable button
    term: "", //    
    needUpdateFriends: false, // ,    ,  
    onlyFriends: false
  , //     
}
let usersReducer = (state = initialState, action) => {
  let stateCopy; //      
    switch (action.type) {
        case TOGGLE_IS_FOLLOWING_PROGRESS: // disable     (follow/unfollow)
            stateCopy = {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.id] //  follow/unfollow ,    followingInProgress id 
                    : state.followingInProgress.filter(id => id !== action.id)//     OK,  id    followingInProgress
            }
            if (bedug_mode) {console.log("users-reducer.js, TOGGLE_IS_FOLLOWING_PROGRESS: ", state, stateCopy)} // 
            return stateCopy; //   
        case SET_USERS:
            stateCopy = {...state, users: action.users};
          if (bedug_mode) {console.log("users-reducer.js, SET_USERS: ", state, stateCopy)} // 
          return stateCopy; //   
        case SET_CURRENT_PAGE:
            stateCopy = {
                ...state,
                currentPage: action.currentPage
            }
          if (bedug_mode) {console.log("users-reducer.js, SET_CURRENT_PAGE: ", state, stateCopy)} // 
          return stateCopy; //   
        case SET_TERM:
            stateCopy = {
                ...state, //   
                term: action.term //   
            }
          if (bedug_mode) {console.log("users-reducer.js, SET_TERM: ", state, stateCopy)} // 
          return stateCopy; //   
        case SET_TOTAL_USERS_COUNT:
            stateCopy = {
                ...state,
                totalUsersCount: action.totalUsersCount
            }
          if (bedug_mode) {console.log("users-reducer.js, SET_TOTAL_USERS_COUNT: ", state, stateCopy)} // 
          return stateCopy; //   
        case TOGGLE_IS_FETCHING:
            stateCopy = {
                ...state,
                isFetching: action.isFetching
            }
          if (bedug_mode) {console.log("users-reducer.js, TOGGLE_IS_FETCHING: ", state, stateCopy)} // 
          return stateCopy; //   
        case NEED_UPDATE_FRIENDS:
          stateCopy = {
                ...state,
              needUpdateFriends: action.needUpdateFriends
            }
          if (bedug_mode) {console.log("users-reducer.js, NEED_UPDATE_FRIENDS: ", state, stateCopy)} // 
          return stateCopy; //   
        case USERS_INITIAL_STATE:
          stateCopy = initialState
          if (bedug_mode) {console.log("users-reducer.js, USERS_INITIAL_STATE: ", state, stateCopy)} // 
          return stateCopy; //   
        case SET_ONLY_FRIENDS:
          stateCopy = {
            ...state,
            onlyFriends: action.onlyFriends
          }
          if (bedug_mode) {console.log("users-reducer.js, SET_ONLY_FRIENDS: ", state, stateCopy)} // 
          return stateCopy; //   
        default:
            return state;
    }
}

export let getUsersThunkCreator = (currentPage, pageSize, term, friend) => {//    
  let getUsersThunk = (dispatch) => { //   
      if (bedug_mode) {console.log("users-reducer.js, getUsersThunkCreator dispatch(toggleIsFetching()) ->TOGGLE_IS_FETCHING ( )" )} // 
      dispatch(toggleIsFetching(true)) //    
        apiUsers.getUsers(currentPage, pageSize, term, friend) //       
            .then((data) => {
                if (bedug_mode) {console.log("users-reducer.js, getUsersThunkCreator .getUsers.then dispatch(toggleIsFetching()) ->TOGGLE_IS_FETCHING ( )" )} // 
                dispatch(toggleIsFetching(false))  //    
                if (bedug_mode) {console.log("users-reducer.js, getUsersThunkCreator .getUsers.then dispatch(setUsers()) ->SET_USERS" )} // 
                dispatch(setUsers(data.items))//     
                if (bedug_mode) {console.log("users-reducer.js, getUsersThunkCreator .getUsers.then dispatch(setUsersTotalCount()) ->SET_TOTAL_USERS_COUNT" )} // 
                dispatch(setUsersTotalCount(data.totalCount))//     
            })

    }
    return getUsersThunk
}

const followUnfollowFlow = (dispatch, userId, currentPage, pageSize, apiMethod, term, friend) => {
  if (bedug_mode) {console.log("users-reducer.js, followUnfollowFlow dispatch(toggleIsFollowingProgerss ->TOGGLE_IS_FOLLOWING_PROGRESS( ID button disable)" )} // 
  dispatch(toggleIsFollowingProgerss(true, userId))// ID     followingInProgress   
    apiMethod(userId)//    // diff apiMethod = postFollow
        .then((response) => {
            if (response.resultCode === 0) {
                apiUsers.getUsers(currentPage, pageSize, term, friend) //       
                    .then((response) => {
                      if (bedug_mode) {console.log("users-reducer.js, followUnfollowFlow postFollow()/deleteFollow().then -> .getUsers.then dispatch(setUsers) ->SET_USERS" )} // 
                      dispatch(setUsers(response.items))//     
                      if (bedug_mode) {console.log("users-reducer.js, followUnfollowFlow postFollow()/deleteFollow().then -> .getUsers.then dispatch(needUpdateFriendsAC) ->NEED_UPDATE_FRIENDS" )} // 
                      dispatch(needUpdateFriendsAC(true))
                      if (bedug_mode) {console.log("users-reducer.js, followUnfollowFlow postFollow()/deleteFollow().then -> .getUsers.then toggleIsFollowingProgerss ->TOGGLE_IS_FOLLOWING_PROGRESS( ID button disable)" )} // 
                      dispatch(toggleIsFollowingProgerss(false, userId))// ID     followingInProgress,  
                    })
            }
        })
}

export let followThunkCreator = (userId, currentPage, pageSize, term, friend) => {// follow  

  return (dispatch) => {//  follow
    if (bedug_mode) {console.log("users-reducer.js, followThunkCreator postFollow() -> followUnfollowFlow" )} // 
    followUnfollowFlow(dispatch, userId, currentPage, pageSize, apiUsers.postFollow.bind(apiUsers), term, friend);
    }
}
export let unfollowThunkCreator = (userId, currentPage, pageSize, term, friend) => {// unfollow  
  return (dispatch) => {//  unfollow
    if (bedug_mode) {console.log("users-reducer.js, unfollowThunkCreator deleteFollow()-> followUnfollowFlow" )} // 
    followUnfollowFlow(dispatch, userId, currentPage, pageSize, apiUsers.deleteFollow.bind(apiUsers), term, friend);
    }
}
export default usersReducer;











