import React from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {
    getProfileThunkCreator,
    putMyProfileThunkCreator,
    putStatusThunkCreator, setEditProfileStatus,
    setprofilePhotoThunkCreator
} from "../../redux/profile-reducer";
import {Navigate} from "react-router-dom"
import {compose} from "redux";
import {bedug_mode} from "../../redux/store-redux";
import NavigateToLoginHoc2 from "../hoc/NavigateToLoginHoc2";
import withRouter2 from "../hoc/withRouter2";

class ProfileContainer extends React.Component {
    componentDidMount() {
        if (bedug_mode) {
            console.log("ProfileContainer.js componentDidMount()")
        } // 
        this.props.getProfileThunkCreator(this.props.userId);//      ID
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        let userId = this.props.userId; //   userId  URL 

        if (userId === 0) {
            userId = this.props.myId //   ID  URL  
        }//      ( ID  URL )    
        if (userId !== this.props.profile.userId) { //    URL ID      ( )
            this.props.getProfileThunkCreator(userId); //      ID
            //   setEditMode  false
        }
    }

    uploadImage = (profilePhoto) => {
        console.log(profilePhoto)
        this.props.setprofilePhotoThunkCreator(profilePhoto, this.props.myId)
    }

    putProfile = ( putProfile ) => { //     
        //        ID      
        let MyProfile = Object.assign({}, {userId: this.props.myId}, putProfile);
        this.props.putMyProfileThunkCreator(MyProfile, this.props.myId)//     
    }

    render() {
        if (this.props.userId === this.props.myId) { /*      */
            return <Navigate to='../profile'/> /*    ID  URL*/
        }

        return <Profile
            {...this.props}
            uploadImage={this.uploadImage} // 
            putProfile={this.putProfile} //       
            dispatch={this.props.dispatch} //    
            editProfileStatus = {this.props.editProfileStatus} //       
            setEditProfileStatus={this.props.setEditProfileStatus}//           
        />
    }
}

let mapStateToProps = (state) => {
    return {
        profile: state.profilePage.profile,
        isAuth: state.auth.isAuth,
        myId: state.auth.myId, // 
        status: state.profilePage.status, // 
        editProfileStatus: state.profilePage.editProfileStatus  //       
    }
}

/*
let mapDispatchToProps = (dispatch) => {
    return {
        getProfileThunkCreator: (userId) => {
            dispatch(getProfileThunkCreator(userId))
        },
        putStatusThunkCreator: (statusTmpInput, myId) => {
            dispatch(putStatusThunkCreator(statusTmpInput, myId))
        },
        setprofilePhotoThunkCreator: (profilePhoto, myId) => {
            dispatch(setprofilePhotoThunkCreator(profilePhoto, myId))
        },
        putMyProfileThunkCreator: (MyProfile, myId) => {
            dispatch(putMyProfileThunkCreator(MyProfile, myId))
        },
        dispatch: dispatch
    }
}
*/

export default compose(
    connect(mapStateToProps, {
        setEditProfileStatus,//           
        getProfileThunkCreator,//      
        putStatusThunkCreator,//    
        setprofilePhotoThunkCreator,//     
        putMyProfileThunkCreator, //     myProfile

    }),
    withRouter2,//   ID  URL     
    NavigateToLoginHoc2// ,   
)
(ProfileContainer)















