import {bedug_mode} from "./store-redux";
import {apiDialogs} from "../components/api/apiLocalStorage";
import {apiDialogs2} from "../components/api/api";

const SEND_MESSAGE = "myApp/dialogs-reducer/SEND-MESSAGE"; //   
const DIALOGS_INITIAL_STATE = "myApp/dialogs-reducer/DIALOGS_INITIAL_STATE";  //   
const SET_MESSAGES = "myApp/dialogs-reducer/SET_MESSAGES";  //     
const DIALOG_USER_ID = "myApp/dialogs-reducer/DIALOG_USER_ID"; //  ID ,   
const DIALOG_LAST_UPDATE_TIME = "myApp/dialogs-reducer/DIALOG_LAST_UPDATE_TIME"; //  ID ,   
const DIALOG_USER_FOLLOWED = "myApp/dialogs-reducer/DIALOG_USER_FOLLOWED"; //  follow/unfollow   (  )
const GET_MY_DIALOG_LIST = "myApp/dialogs-reducer/GET_MY_DIALOG_LIST"; //   

export let sendMessageCreator = (formDataNewMessage) => { //   
  return {type: SEND_MESSAGE, formDataNewMessage}
};
export let dialogsInitialState = () => { //    
  return {type: DIALOGS_INITIAL_STATE,}
};
export let setMessages = (updatedMessages) => { //       messages2
  return {type: SET_MESSAGES, updatedMessages}
};
export let setdialogUserID = (dialogUserID) => { //       messages2
  return {type: DIALOG_USER_ID, dialogUserID}
};
export let setDialogLastUpdateTime = (dialogLastUpdateTime) => { //       
  return {type: DIALOG_LAST_UPDATE_TIME, dialogLastUpdateTime}
};
export let setDialogUserFollowed = (dialogUserFollowed) => { //       
  return {type: DIALOG_USER_FOLLOWED, dialogUserFollowed}
};
export let getMyDialogList = (myDialogList) => { //      
  return {type: GET_MY_DIALOG_LIST, myDialogList}
};

let initialState = { //    
  messages2: [],
  dialogUserID: null, // ID    
  dialogUserFollowed: false, //  followed? ()
  dialogLastUpdateTime: null, //     
  dialogs2: [] //  .    LocalStorage    
}

let dialogsReducer = (state = initialState, action) => { //  
  let stateCopy; //      
  switch (action.type) {
    case SEND_MESSAGE: //        
      let body = action.formDataNewMessage;
      stateCopy = {
        ...state,
        messages: [...state.messages, {id: 6, message: body}], //   ()
      }
      if (bedug_mode) {
        console.log("dialogs-reducer.js, SEND_MESSAGE: ", state, stateCopy)
      } // 
      return stateCopy
    case DIALOGS_INITIAL_STATE: //        
      stateCopy = initialState;
      if (bedug_mode) {
        console.log("dialogs-reducer.js, DIALOGS_INITIAL_STATE: ", state, stateCopy)
      } // 
      return stateCopy
    case SET_MESSAGES: //        
      stateCopy = {
        ...state,
        messages2: action.updatedMessages, //  
      }
      if (bedug_mode) {
        console.log("dialogs-reducer.js, SET_MESSAGES(LocalStorage): ", state, stateCopy)
      } // 
      return stateCopy
    case DIALOG_USER_ID: //    ID ,   
      stateCopy = {
        ...state,
        dialogUserID: action.dialogUserID, //  ID ,   
      }
      if (bedug_mode) {
        console.log("dialogs-reducer.js, DIALOG_USER_ID: ", state, stateCopy)
      } // 
      return stateCopy
    case DIALOG_LAST_UPDATE_TIME: //        
      if (state.dialogLastUpdateTime !== action.dialogLastUpdateTime) { // ,        (  )
        stateCopy = {
          ...state,
          dialogLastUpdateTime: action.dialogLastUpdateTime, //       
        }
        if (bedug_mode) {
          console.log("dialogs-reducer.js, DIALOG_LAST_UPDATE_TIME: ", state, stateCopy)
        } // 
        return stateCopy
      }
      return state //    ,    
    case DIALOG_USER_FOLLOWED: //    follow/unfollow   (   )
      stateCopy = {
        ...state,
        dialogUserFollowed: action.dialogUserFollowed, //   follow/unfollow   (   )
      }
      if (bedug_mode) {
        console.log("dialogs-reducer.js, DIALOG_USER_FOLLOWED: ", state, stateCopy)
      } // 
      return stateCopy
    case GET_MY_DIALOG_LIST: //     
      stateCopy = {
        ...state,
        dialogs2: action.myDialogList, //    
      }
      if (bedug_mode) {
        console.log("dialogs-reducer.js, GET_MY_DIALOG_LIST: ", state, stateCopy)
      } // 
      return stateCopy
    default:
      return state;
  }
}

export let getDialogsThunkCreator = (myId, userId) => {//    
  if (bedug_mode) {
    console.log("getDialogsThunkCreator")
  }

  let getDialogsThunk = async (dispatch) => {//    
    let updatedMessages = await apiDialogs.getDialog(myId, userId)
    if (bedug_mode) {
      console.log("dialogs-reducer.js, getDialogsThunkCreator->: dispatch(setMessages)->SET_MESSAGES")
    } // 
    dispatch(setMessages(updatedMessages))
  }
  return getDialogsThunk
}
export let sendDialogsThunkCreator = (formDataNewMessage, myId, MyName, MyPhoto, userId) => {//     
  if (bedug_mode) {
    console.log("sendDialogsThunkCreator")
  }

  let sendDialogsThunk = async (dispatch) => {//      
    let updatedMessages = await apiDialogs.postDialog(formDataNewMessage, myId, MyName, MyPhoto, userId)
    if (bedug_mode) {
      console.log("dialogs-reducer.js, sendDialogsThunkCreator->: dispatch(setMessages)->SET_MESSAGES")
    } // 
    dispatch(setMessages(updatedMessages))
  }
  return sendDialogsThunk
}

export let getDialogLastUpdateTimeTnkCrt = (myId, userId) => {//    
  let getDialogLastUpdateTimeTnk = async (dispatch) => {//    
    let dialogLastUpdateTime = await apiDialogs.getUpdateTime(myId, userId) //     
    dispatch(setDialogLastUpdateTime(dialogLastUpdateTime)) //   BLL     
  }
  return getDialogLastUpdateTimeTnk
}

export let deleteMessageThunkCreator = (messageID, myId, userId) => {//    
  let deleteMessageThunk = async (dispatch) => {//     
    let dialogAfterDeleteMessage = await apiDialogs.deleteMessage(messageID, myId, userId) //         
    if (bedug_mode) {
      console.log("dialogs-reducer.js, deleteMessageThunkCreator->: dispatch(setMessages)->SET_MESSAGES")
    } // 
    dispatch(setMessages(dialogAfterDeleteMessage))//      
  }
  return deleteMessageThunk
}

export let getFollowThunkCreator = (dialogUserID) => {//  follow/unfollow      
  let getFollowThunk = async (dispatch) => {// 
    let dialogUserFollowed = await apiDialogs2.getFollow(dialogUserID) //  follow/unfollow      
    if (bedug_mode) {console.log("dialogs-reducer.js, getFollowThunkCreator->: dispatch(setDialogUserFollowed)->DIALOG_USER_FOLLOWED")} // 
    dispatch(setDialogUserFollowed(dialogUserFollowed))//    follow/unfollow  
  }
  return getFollowThunk
}
export let getMyDialogListThunkCreator = (myId) => {//   
  let getMyDialogListThunk = async (dispatch) => {// 
    let myDialogList = await apiDialogs.getDialogListMyID(myId) //   
    //  if (bedug_mode) {console.log("dialogs-reducer.js, getMyDialogListThunkCreator->: dispatch(setDialogUserFollowed)->DIALOG_USER_FOLLOWED")} // 
    dispatch(getMyDialogList(myDialogList))//     
  }
  return getMyDialogListThunk
}
export let updateDialogListThunkCreator = (userId1, userId2, Name2, Photo2) => {
  //   (      ) -   localStorage.
  let updateDialogListThunk = async (dispatch) => {// 
      await apiDialogs.updateDialogListUserId(userId1, userId2, Name2, Photo2) //   
  }
  return updateDialogListThunk
}

export let deleteDialogThunkCreator = (dialogId, userId1, userId2) => {
  //    
  let deleteDialogThunk = async (dispatch) => {// 
      await apiDialogs.deleteDialog(dialogId, userId1, userId2) //      
//      -    
  }
  return deleteDialogThunk
}

export default dialogsReducer;

