const GetDate = () => {
    const Data = new Date();
    const Year = Data.getFullYear();
    const Month = Data.getMonth();
    const Day = Data.getDate();
    const Hour = Data.getHours();
    const Minutes = Data.getMinutes();
    const Seconds = Data.getSeconds();
    let fMonth;

    switch (Month)
    {
        case 0: fMonth="."; break;
        case 1: fMonth="."; break;
        case 2: fMonth="."; break;
        case 3: fMonth="."; break;
        case 4: fMonth=""; break;
        case 5: fMonth="."; break;
        case 6: fMonth="."; break;
        case 7: fMonth="."; break;
        case 8: fMonth="."; break;
        case 9: fMonth="."; break;
        case 10: fMonth="."; break;
        case 11: fMonth="."; break;
        default: fMonth=""; break;
    }
    let DataReturn2 = {
        "Year": Year,
        "Month": fMonth,
        "Day": Day,
        "Hour": Hour,
        "Minutes": Minutes,
        "Seconds": Seconds
    };

    return DataReturn2
}
export default GetDate
