import {getAuthMeThunkCreator} from "./auth-reducer";
import {bedug_mode} from "./store-redux";
import {apiCommon} from "../components/api/apiLocalStorage";

const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  
const APP_INITIAL_STATE = "myApp/app-reducer/APP_INITIAL_STATE"; //   
const SET_INFO_MODE = "myApp/app-reducer/SET_INFO_MODE"; //      

export let setInitialisedApp = () => { //     
  return {type: SET_INITIALISED_APP}
};
export let appInitialState = () => { //    
  return {type: APP_INITIAL_STATE}
};
export let setInfoMode = (info_mode) => { //     
  return {type: SET_INFO_MODE, info_mode}
};

let initialState = { //     
  initialisedApp: false, //   ?
  bedug_mode: false, //      
  info_mode: true, //      
}

let appReducer = (state = initialState, action) => {//  
  let stateCopy; //      
  switch (action.type) {
    case SET_INITIALISED_APP: //   
      stateCopy = {
        ...state, //   
        initialisedApp: true, //      true
      }
      if (bedug_mode) {console.log("app-reducer.js, SET_INITIALISED_APP: ", state, stateCopy)} // 
      return stateCopy; //     
    case APP_INITIAL_STATE: //    
      stateCopy = initialState
      if (bedug_mode) {console.log("app-reducer.js, APP_INITIAL_STATE: ", state, stateCopy)} // 
      return stateCopy; //     
    case SET_INFO_MODE: //      ()  
      stateCopy = {
        ...state, //   
        info_mode: action.info_mode, //    
      }
      if (bedug_mode) {console.log("app-reducer.js, APP_INITIAL_STATE: ", state, stateCopy)} // 
      return stateCopy; //     
    default:
      return state; //     
  }
}

export let initialisedAppThunkCreator = () => {//   
  let initialisedAppThunk = (dispatch) => { //    
    const promise1 = dispatch(getAuthMeThunkCreator()) //  
    Promise.all([promise1])
      .then(() => {

        //      (     )
        if (bedug_mode) {console.log("app-reducer.js, initialisedAppThunkCreator.then: ", "setInitialisedApp()->SET_INITIALISED_APP")} // 
        dispatch(setInitialisedApp()) //     true

      })
  }
  return initialisedAppThunk;
}
export let setInfoModeThunkCreator = (info_mode) => {//    
  let setInfoModeThunk = async (dispatch) => { //     
    let info_modeFromLS = await apiCommon.putInfoMode(info_mode) //  info_mode  LocalStorage    
    dispatch(setInfoMode(info_modeFromLS)) //   BLL  info_mode  LocalStorage
   }
  return setInfoModeThunk;
}
export let getInfoModeThunkCreator = () => {//    
  let getInfoModeThunk = async (dispatch) => { //     
    let info_modeFromLS = await apiCommon.getInfoMode() //  info_mode  LocalStorage    
    dispatch(setInfoMode(info_modeFromLS)) //   BLL  info_mode  LocalStorage
   }
  return getInfoModeThunk;
}

export default appReducer;










