import React from "react";
import {Formik, Form} from "formik"; //     
import * as Yup from 'yup' //       Yup
import classes from './EditProfile.module.css'
//import DisplayFormikState from "../../../common/formikCommon/DisplayFormikState"
import {MyCheckbox, MyTextInput} from "../../../common/formikCommon/MyFieldsBS"
import Button from "react-bootstrap/Button";

let myValidationSchema = Yup.object({ //    required,    
})

const EditProfileFormik = ({putProfile, setEditMode, profile, editProfileStatus, setEditProfileStatus}) => { //     ,     

    let e = React.createElement
    let myInitialValues = { //   
        FullName: profile.fullName,
        AboutMe: profile.aboutMe,
        LookingForAJob: profile.lookingForAJob,
        LookingForAJobDescription: profile.lookingForAJobDescription,
        contacts: profile.contacts, //     
    }

    const myOnSubmit = (values, {resetForm}) => { //   
        putProfile(values) // ,     
        resetForm()//     
        //  setEditMode(false) //       

    }

    return (
        e(Formik, {
            initialValues: myInitialValues, //   
            validationSchema: myValidationSchema, //  
            onSubmit: myOnSubmit //   
        }, ({
                handleReset,//  
            }) => ( //           (  )
            e(Form, {className: classes.MyPosts},

                e('div', {},
                    e('div', // 
                        {className: classes.HeaderEditProfileForm},
                        ' '
                    ),

                    e(MyTextInput, // :
                        {
                            label: "",
                            name: 'FullName',
                            type: 'text',
                            placeholder: '',
                            leftLabelLength: "5rem" //   input  label  
                        }
                    ),

                    e(MyTextInput,// 
                        {label: " ", name: 'AboutMe', type: 'text', placeholder: ' ', leftLabelLength: "5rem"}, //   input  label  
                    ),

                    e(MyCheckbox, {name: 'LookingForAJob'}, //  
                        "  "
                    ),

                    e(MyTextInput, { //  
                            label: "", name: 'LookingForAJobDescription',
                            type: 'textarea', placeholder: '', leftLabelLength: "5rem" //   input  label  
                        }
                    ),

                    e('h4', {},//     
                        ":"
                    ),

                    e('div', {className: classes.EditProfileContactsFields},
                        Object.keys(profile.contacts).map((c) => { //   , 
                            return e('div', {key: c},
                                e(
                                    MyTextInput,
                                    {
                                        label: c,
                                        name: 'contacts[' + c + ']',
                                        type: 'text',
                                        placeholder: c,
                                        leftLabelLength: "5rem" //   input  label  
                                    },
                                ),
                                e('div', {}, //    
                                    editProfileStatus.map(err => {//         
                                        if (err.toLowerCase().includes(c.toLowerCase())) { //     ""     
                                            return e('div', {key: err, className: classes.errorText}, err )//       
                                        }
                                        return null
                                    })
                                ),
                            )
                        })
                    ),
                    e('br'), // 

                    //     
                    e(Button, {
                        type: 'button',
                        variant: "warning",
                        onClick: () => { //     
                            handleReset()//     
                            setEditProfileStatus([]) //      
                        }
                    }, ''),

                    " ", //  

                    //  
                    e(Button, {type: 'submit'}, ''),

                    " ", //  

                    //
                    e(Button, {
                        variant: "secondary",
                        onClick: () => { //     
                            setEditMode(false)//       
                            setEditProfileStatus([]) //      
                        }
                    }, ''),
                )
            )
        ))
    )
}

export default EditProfileFormik

