import {bedug_mode} from "./store-redux";

const SET_THEME = "myApp/dark-light-reducer/SET_THEME"; //  

export let setTheme = (themeUpdate) => { //   
  return {type: SET_THEME, themeUpdate }
};

let initialState = { //   
  themeBLL: "light", //   bll  
}

let darkLightReducer = (state = initialState, action) => {//  
  let stateCopy; //      
  switch (action.type) {
    case SET_THEME: //   
      stateCopy = {
        ...state, //   
        themeBLL: action.themeUpdate, //    BLL
      }
      if (bedug_mode) {console.log("dark-light-reducer.js, SET_THEME: ", state, stateCopy)} // 
      return stateCopy; //     
    default:
      return state; //     
  }
}

export default darkLightReducer;










