import {bedug_mode} from "./store-redux";
import {apiFeedBack2} from "../components/api/api";

const SET_FEED_BACK_STATUS = "myApp/feedback-reducer/SET_FEED_BACK_STATUS"; //     feedback

let initialState = { //   
  feedBackStatus: "" //    (feedBack) -       
}

export let setFeedBackStatus = (feedBackStatus) => { //    feedBackStatus
  return {type: SET_FEED_BACK_STATUS, feedBackStatus}
};


let feedBackReducer = (state = initialState, action) => {//  
  let stateCopy; //      
  switch (action.type) {
    case SET_FEED_BACK_STATUS: //   feedBackStatus
      stateCopy = {
        ...state,
        feedBackStatus: action.feedBackStatus
      }
      if (bedug_mode) {
        console.log("feedback-reducer.jsx, SET_FEED_BACK_STATUS: ", state, stateCopy)
      } // 
      return stateCopy; //     
    default:
      return state; //     
  }
}

export let postFeedBackThunkCreator2 = (data) => {//   
  let postFeedBackThunk2 = async (dispatch) => { //   
    await apiFeedBack2.postFeedBack2(data) //
      .then(() => dispatch(setFeedBackStatus("    ")))//    BLL "    "
      .catch((err) => dispatch(setFeedBackStatus((err.toString()))));//          BLL
  }
  return postFeedBackThunk2;
}

export default feedBackReducer;
