import {apiProfile} from "../components/api/api";
import {bedug_mode} from "./store-redux";
import {friendsInitialState} from "./sidebar-reducer";
import {dialogsInitialState} from "./dialogs-reducer";
import {profileInitialState} from "./profile-reducer";
import {usersInitialState} from "./users-reducer";

const SET_MY_DATA = "myApp/auth-reducer/SET_MY_DATA"; //        (ID, Email, login, isAuth)
const AUTH_INITIAL_STATE = "myApp/auth-reducer/AUTH_INITIAL_STATE"; //   
const SET_CAPTCHA_URL = "myApp/auth-reducer/SET_CAPTCHA_URL"; //  URL 
const SET_LOGIN_ERROR= "myApp/auth-reducer/SET_LOGIN_ERROR"; //   
const SET_MY_PROFILE = "myApp/auth-reducer/SET_MY_PROFILE"; //      

export let setMyProfile = (myProfile) => { //       
    return {type: SET_MY_PROFILE, myProfile}
};
export let setAuthData = (id, email, login, isAuth) => { //     ID, Email, login
    return {type: SET_MY_DATA, id, email, login, isAuth}
};
export let authInitialState = () => { //     
    return {type: AUTH_INITIAL_STATE}
};
export let setCaptchaURL = (captchaURL) => { //    URL    
    return {type: SET_CAPTCHA_URL, captchaURL}
};
export let setLoginError = (loginError) => { //     ID, Email, login
    return {type: SET_LOGIN_ERROR, loginError}
};


let initialState = { //      
    myId: null, //  ID  
    myEmail: null,//  Email  
    myLogin: null,//    
    isAuth: false, //  
    myProfile: null, //     
    captchaURL: null, // URL   5  
    loginError: null, //    
}

let authReducer = (state = initialState, action) => { //     
    let stateCopy; //      
    switch (action.type) {
        case SET_MY_DATA: //    id, email, login
            stateCopy = {
                ...state,
                myId: action.id,
                myEmail: action.email,
                myLogin: action.login,
                isAuth: action.isAuth,
            }
            if (bedug_mode) {
                console.log("auth-reducer.jsx, SET_MY_DATA: ", state, stateCopy)
            } // 

            return stateCopy; //     
        case SET_MY_PROFILE: //     
            stateCopy = {
                ...state,
                myProfile: action.myProfile
            }
            if (bedug_mode) {
                console.log("auth-reducer.jsx, SET_MY_PROFILE: ", state, stateCopy)
            } // 
            return stateCopy; //     
        case AUTH_INITIAL_STATE: //    
            stateCopy = initialState
            if (bedug_mode) {
                console.log("auth-reducer.jsx, AUTH_INITIAL_STATE: ", state, stateCopy)
            } // 
            return stateCopy; //     
        case SET_CAPTCHA_URL: //    
            stateCopy = {
                ...state,
                captchaURL: action.captchaURL
            }
            if (bedug_mode) {
                console.log("auth-reducer.jsx, SET_CAPTCHA_URL: ", state, stateCopy)
            } // 
            return stateCopy; //     
        case SET_LOGIN_ERROR: //      
            stateCopy = {
                ...state,
                loginError: action.loginError
            }
            if (bedug_mode) {
                console.log("auth-reducer.jsx, SET_LOGIN_ERROR: ", state, stateCopy)
            } // 
            return stateCopy; //     
        default:
            return state; //     
    }
}

export let getAuthMeThunkCreator = () => {//  ?.    
    let getAuthMeThunk = async (dispatch) => {
        const response1 = await apiProfile.getAuthMe() //  ?
        if (response1.resultCode === 0) { //  
            if (bedug_mode) {
                console.log("auth-reducer.jsx, getAuthMeThunkCreator.await getAuthMe() : dispatch(setAuthData)->SET_MY_DATA  ( )")
            } // 
            dispatch(setAuthData(
                response1.data.id, //     ID
                response1.data.email, //     
                response1.data.login, //     
                true //    
            ))//    

            const response2 = await apiProfile.getProfile(response1.data.id)//     
            if (bedug_mode) {
                console.log("auth-reducer.jsx, getAuthMeThunkCreator.await(getAuthMe)->await .getProfile() : dispatch(setMyProfile()->SET_MY_PROFILE")
            } // 
            dispatch(setMyProfile(response2))//     
        }
        if (response1.resultCode !== 0) { //  
            dispatch(authInitialState()) //   
        }
    }
    return getAuthMeThunk;
}

export let postLoginThunkCreator = (email, password, rememberme, captchaURL) => {//  
    let postLoginThunk = async (dispatch) => { //    
        const response = await apiProfile.postLogin(email, password, rememberme, captchaURL) //       
        if (response.resultCode === 0) { //     
            if (bedug_mode) {
                console.log("auth-reducer.jsx, postLoginThunkCreator.await .postLogin(): dispatch(getAuthMeThunkCreator())")
            } // 
            dispatch(getAuthMeThunkCreator()) //      
        } else { //      
            let message =  //    message -   
                !response.messages[0] //     
                    ? "no responce from server" //   
                    : response.messages[0] //     
            if (bedug_mode) {
                console.log("auth-reducer.jsx, postLoginThunkCreator.await /      : dispatch(action) //    ")
            } // 
            if (response.resultCode === 10) { //         
                dispatch(getCaptchaThunkCreator())
            }
            dispatch(setLoginError(message)) //    
        }
    }
    return postLoginThunk;
}
export let deleteLoginThunkCreator = () => {//  
    let deleteLoginThunk = async (dispatch) => { //    
        const response = await apiProfile.deleteLogin() //    
        if (response.resultCode === 0) { //    
            setTimeout(() => {

                if (bedug_mode) {
                    console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(dialogsInitialState())->DIALOGS_INITIAL_STATE")
                } // 
                dispatch(dialogsInitialState())//    

                if (bedug_mode) {
                    console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(authInitialState())->AUTH_INITIAL_STATE")
                } // 
                dispatch(authInitialState())//    

                if (bedug_mode) {
                    console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(profileInitialState())->PROFILE_INITIAL_STATE")
                } // 
                dispatch(profileInitialState())//    


                if (bedug_mode) {
                    console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(friendsInitialState())->FRIENDS_INITIAL_STATE")
                } // 
                dispatch(friendsInitialState())//  FriendList  

                if (bedug_mode) {
                    console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(usersInitialState())->USERS_INITIAL_STATE")
                } // 
                dispatch(usersInitialState())//  UsersBS  

            }, 300)
        } else {
            console.log(response.messages) //      
        }
    }
    return deleteLoginThunk;
}

export let getCaptchaThunkCreator = () => {//   
    let getCaptchaThunk = async (dispatch) => { //    
        const response2 = await apiProfile.getCaptcha() //  
        if (bedug_mode) {
            console.log("auth-reducer.jsx, postLoginThunkCreator.await .getCaptcha(): dispatch()")
        } // 
        dispatch(setCaptchaURL(response2.url)) //      
    }
    return getCaptchaThunk;
}

export default authReducer;










